.burgerMenuIcon {
    cursor: pointer;
    height: 24px;
    width: 24px;
    margin: 20px 0 0 70px;
}

.header {
    width: 100%;
    height: 60px;
    box-shadow: 0 0 2px 0 rgba(29, 33, 38, 0.1), 0 0 40px 0 rgba(29, 33, 38, 0.13);
    background: #fff;
    display: flex;
    justify-content: space-between;
}

.header h1 {
    font-weight: 600;
    font-size: 24px;
    color: #000;
    margin: 12px 70px 0 0;
}